.section-title {
  font-size: 2.55rem;
  font-weight: 300;
  text-align: center;

  @include atSmall {
    font-size: 3.75rem;
  }

  strong {
    font-weight: 500;
  }

  &__icon {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0.6rem;

    @include atSmall {
      position: relative;
      top: 8px;
      margin-right: 0.5rem;
      margin-left: auto;
      display: inline-block;
      margin-bottom: 0;
    }
  }

  &--blue {
    color: $main-blue;
  }

  &--less-margin {
    margin-bottom: 1.3rem;
    margin-top: 0;
  }
}
